import axios from "axios";
import React, {useEffect, useState} from "react";
import {CButton, CCard, CCardBody, CCardHeader} from "@coreui/react";
import CountUp from "react-countup";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import {useForm} from "react-hook-form";

import '../../../utils/Orange.css';
import flights from "../travel.jpeg";
import Collapsible from 'react-collapsible';
import plus from "../../../assets/images/plus.png";
import NoData from "../../../assets/images/no-data.png";
import WidgetsDropdownFlights from "../../widgets/WidgetsDropdownFlights";
import WidgetsCollectionMarkups from "../../widgets/WidgetsCollectionMarkups";
import {exportToExcel, getCookieValue, ipAddress, tokenName, errorToast} from "../../../utils/Common";

const FailureLogsFlights = () => {
  const [expandedRows, setExpandedRows] = useState({});

  let {
    register,
    handleSubmit,
    reset,
    formState: {errors}
  } = useForm();

  const toggleExpand = (id) => {
    setExpandedRows((prevState) => ({
      ...prevState,
      [id]: !prevState[id]
    }));
  };

  const [stats, setStats] = useState([]);
  const [stats2, setStats2] = useState([]);
  const [loader, setLoader] = useState(true)
  const [showData, setShowData] = useState(false);

  function resetHandler() {
    reset();
    setStats(stats2)
  }

  function typedSearching(Params) {
    const recordset = stats2;
    const {BookingID, PNR} = Params;

    if (BookingID === "" || PNR === "") {
      errorToast('Please Enter Booking ID and PNR');
      return false;
    }

    let filtered;
    if (BookingID && PNR) {
      filtered = recordset.filter(obj =>
        obj.bookingId?.[0]?.includes(BookingID) ||
        obj.GDSPNR?.[0]?.includes(PNR)
      );
    } else if (PNR) {
      filtered = recordset.filter(obj => obj.GDSPNR?.[0]?.includes(PNR));
    } else if (BookingID) {
      filtered = recordset.filter(obj => obj.bookingId?.[0]?.includes(BookingID));
    }
    // console.log(filtered);

    if (filtered.length === 0) {
      setShowData(true);
      setStats(filtered);
    } else {
      setShowData(false);
      setStats(filtered);
    }
  }

  function showFlightStats() {
    let token = getCookieValue(tokenName);

    axios.get(ipAddress + "admin/failed-bookings", {
      headers: {'Authorization': `Bearer ${token}`,}
    }).then((response) => {
      // console.log(response.data);
      // console.log(response.data.recordset);
      // localStorage.setItem("flightsStats", JSON.stringify(response.data.recordset));
      setStats(response.data.recordset);
      setStats2(response.data.recordset);
      setLoader(false)
    }).catch(e => {
      // console.log(e)
      setLoader(false)
    })
  }

  useEffect(() => {
    showFlightStats();
  }, []);

  const myStyle = {
    backgroundImage: `url(${flights})`,
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    // width: '100vw',
    height: '100vh',
  };

  return (
    <>
      {loader ? <div className="text-center"><span className="spinner-border text-primary"></span></div> : <>
        <CCard className="mb-4">
          <CCardHeader>
            <div style={{display: 'flex', justifyContent: 'space-between'}}>
              <div style={{flex: 1}}><strong> Search By : </strong><br/></div>
            </div>
            <br/>
            <form onSubmit={handleSubmit(typedSearching)}>
              <div className="row">
                {/* Booking ID */}
                <div className="col-sm-4">
                  <span>Booking ID</span>
                  <input className="form-control" type="text" placeholder="Booking RefNo"
                         {...register('BookingID')}/>
                </div>

                {/* PNR */}
                <div className="col-sm-4">
                  <span>PNR</span>
                  <input className="form-control" type="text" placeholder="PNR"
                         {...register('PNR')}/>
                </div>

                <div className="col-sm-4">
                  <span style={{visibility: "hidden"}}>&nbsp;</span>
                  <br/>
                  <Button variant="primary" type="submit">
                    Search
                  </Button>

                  <Button variant="danger" type="button" className="ms-3 text-white" onClick={resetHandler}>
                    Reset
                  </Button>
                </div>
              </div>

              <div className="row" style={{marginTop: "5px"}}>
                <div className="col-sm-4"></div>

                <div className="col-sm-2 offset-6">
                  <span style={{visibility: "hidden"}}>Booking Status</span>
                  <br/>
                  <Button type='button' className='text-white' variant="success"
                          onClick={() => exportToExcel('flightStats')}>
                    Export To Excel
                  </Button>
                </div>
              </div>
            </form>
          </CCardHeader>

          <CCardBody>
            <h4 className="mt-2 mb-4">Flights Logs</h4>
            <div style={{height: "335px"}} id="flightStats" className="table-responsive">
              <Table responsive hover>
                <thead style={{position: "sticky", top: 0}}>
                <tr>
                  <th style={{fontSize: "14px"}}>Booking&nbsp;ID</th>
                  <th style={{fontSize: "14px"}} className="text-center">Agent&nbsp;Email</th>
                  <th style={{fontSize: "14px"}} className="text-center">Payment&nbsp;Status</th>
                  <th style={{fontSize: "14px"}}>Booking&nbsp;Status</th>
                  <th style={{fontSize: "14px"}} className="text-center">PNR</th>
                  <th style={{fontSize: "14px"}}>Supplier</th>
                  <th style={{fontSize: "14px"}}>Published&nbsp;Amount</th>
                  <th style={{display: "none"}}>Midoffice&nbsp;Markup</th>
                  <th style={{display: "none"}}>Platform&nbsp;Fee</th>
                  <th style={{display: "none"}}>Tax&nbsp;On&nbsp;Platform&nbsp;Fee</th>
                  <th style={{fontSize: "14px"}}>Sectors</th>
                </tr>
                </thead>

                <tbody>
                {stats?.length > 0 && Object.values(
                  stats.reduce((acc, item) => {
                    if (!acc[item.bookingId[0]]) {
                      acc[item.bookingId[0]] = [];
                    }
                    acc[item.bookingId[0]].push(item);
                    return acc;
                  }, {})
                ).reverse().map((group, index) => (

                  <React.Fragment key={index}>
                    <tr>
                      <td className="expandable name"
                          style={{color: "blue", fontSize: "14px"}}
                          onClick={() => toggleExpand(group[0].bookingId[0])}>
                        <img src={plus} alt="Icon"/>
                        &nbsp;
                        {group[0].bookingId[0]}
                      </td>

                      <td style={{fontSize: "14px"}}
                          className="text-center">{group[0].agentEmail}</td>
                      <td style={{fontSize: "14px"}} className="text-center">
                        <b>{group[0].paymentStatus !== "undefined" ? group[0].paymentStatus : ""}</b>
                      </td>
                      <td style={{fontSize: "14px"}} className="text-center">
        <span
          className={`badge ms-auto ${group[0].BookingStatus === "Ticketed" ? 'bg-success' : group[0].BookingStatus === "HOLD" ? 'bg-warning' : group[0].BookingStatus === "cancelled" ? 'bg-danger' : 'bg-orange'}`}
        >
          {group[0].BookingStatus}
        </span>
                      </td>
                      <td style={{fontSize: "14px"}}
                          className="text-center">{group[0].GDSPNR[0]}</td>
                      <td style={{fontSize: "14px"}}
                          className="text-center">{group[0].supplier}</td>
                      <td style={{fontSize: "14px"}}
                          className="text-center">{group[0].totalAmount}</td>
                      <td style={{fontSize: "14px", display: "none"}}
                          className="text-center">{group[0].adminMarkup}</td>
                      <td style={{fontSize: "14px", display: "none"}}
                          className="text-center">{group[0].platformFee}</td>
                      <td style={{fontSize: "14px", display: "none"}}
                          className="text-center">{group[0].platformTax}</td>
                      <td style={{fontSize: "14px"}}
                          className="text-center">{group[0].origin} - {group[0].destination}</td>
                    </tr>

                    {expandedRows[group[0].bookingId[0]] &&
                      group.map((item, idx) => (
                        <tr key={`expandedRow-${item.bookingId[0]}-${idx}`}>
                          <td colSpan="8" className="gray-row">
                            <b>Name
                              :</b> {item.salutation} {item.firstName} {item.lastName}
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            <b>Paxtype:</b> {item.paxType === "1" || item.paxType === "ADULT" ? "Adult" : item.paxType === "2" || item.paxType === "CHILD" ? "Child" : item.paxType === "3" || item.paxType === "INFANT" ? "Infant" : ""}
                            {/*&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;*/}
                            {/*<b>Published Fare :</b> {item.publishFareFromApi}*/}
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            {item.TripType === "ROUND_TRIP_CUSTOM" && item.Trip !== "undefined" && (
                              <>
                                <b>Trip
                                  :</b> {item.Trip}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                              </>
                            )}

                          </td>
                        </tr>
                      ))}
                  </React.Fragment>

                ))}


                </tbody>
              </Table>
            </div>
          </CCardBody>
        </CCard>
      </>
      }
    </>
  )
}

export default FailureLogsFlights
